___CSS_LOADER_EXPORT___.push([module.id, "\n.terminal-settings-view[data-v-87897b46] {\n  max-width: 1200px;\n  margin: 0 auto;\n  padding: 20px;\n}\n.loading-container[data-v-87897b46] {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px;\n}\n.settings-container[data-v-87897b46] {\n  padding: 10px;\n}\n.setting-item[data-v-87897b46] {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n}\n.setting-info[data-v-87897b46] {\n  flex: 1;\n}\n.setting-info h3[data-v-87897b46] {\n  margin-top: 0;\n  margin-bottom: 10px;\n}\n.setting-info p[data-v-87897b46] {\n  margin-top: 0;\n  color: #666;\n}\n.setting-control[data-v-87897b46] {\n  margin-left: 20px;\n}\n.terminal-info[data-v-87897b46] {\n  background-color: #f8f9fa;\n  border-radius: 6px;\n  padding: 20px;\n  margin-top: 20px;\n}\n.terminal-info h3[data-v-87897b46] {\n  margin-top: 0;\n  margin-bottom: 15px;\n}\n.terminal-info h4[data-v-87897b46] {\n  margin-top: 20px;\n  margin-bottom: 10px;\n}\n.terminal-info ul[data-v-87897b46] {\n  margin: 0;\n  padding-left: 20px;\n}\n.terminal-info li[data-v-87897b46] {\n  margin-bottom: 5px;\n}\ncode[data-v-87897b46] {\n  background-color: #e9ecef;\n  padding: 2px 6px;\n  border-radius: 4px;\n  font-family: monospace;\n}\n", "",{"version":3,"sources":["webpack://./resources/views/TerminalSettingsView.vue"],"names":[],"mappings":";AA2IA;EACE,iBAAiB;EACjB,cAAc;EACd,aAAa;AACf;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,aAAa;AACf;AAEA;EACE,aAAa;AACf;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,mBAAmB;AACrB;AAEA;EACE,OAAO;AACT;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;AAEA;EACE,aAAa;EACb,WAAW;AACb;AAEA;EACE,iBAAiB;AACnB;AAEA;EACE,yBAAyB;EACzB,kBAAkB;EAClB,aAAa;EACb,gBAAgB;AAClB;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;AAEA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;AAEA;EACE,SAAS;EACT,kBAAkB;AACpB;AAEA;EACE,kBAAkB;AACpB;AAEA;EACE,yBAAyB;EACzB,gBAAgB;EAChB,kBAAkB;EAClB,sBAAsB;AACxB","sourcesContent":["<template>\n  <div class=\"terminal-settings-view\">\n    <Card>\n      <template #title>\n        Terminal Settings\n      </template>\n      <template #subtitle>\n        Configure terminal access and database commands\n      </template>\n      <template #content>\n        <div v-if=\"state.isLoading\" class=\"loading-container\">\n          <ProgressSpinner />\n          <p>Loading settings...</p>\n        </div>\n        \n        <div v-else class=\"settings-container\">\n          <div class=\"setting-item\">\n            <div class=\"setting-info\">\n              <h3>Enable Terminal</h3>\n              <p>Allow access to the WordPress Debug Terminal</p>\n            </div>\n            <div class=\"setting-control\">\n              <InputSwitch v-model=\"state.terminalEnabled\" @change=\"updateSettings\" />\n            </div>\n          </div>\n          \n          <Divider />\n          \n          <div class=\"setting-item\">\n            <div class=\"setting-info\">\n              <h3>Enable Database Commands</h3>\n              <p>Allow super administrators to execute database commands in the terminal</p>\n              <Message severity=\"warn\">\n                <span>Database commands can only be executed by super administrators for security reasons.</span>\n              </Message>\n            </div>\n            <div class=\"setting-control\">\n              <InputSwitch v-model=\"state.dbCommandsEnabled\" @change=\"updateSettings\" :disabled=\"!state.terminalEnabled\" />\n            </div>\n          </div>\n          \n          <Divider />\n          \n          <div class=\"terminal-info\">\n            <h3>Available Commands</h3>\n            <p>The terminal supports WP-CLI style commands. Type <code>wp</code> to see available WordPress commands.</p>\n            \n            <h4>Examples:</h4>\n            <ul>\n              <li><code>wp core version</code> - Show WordPress version</li>\n              <li><code>wp plugin list</code> - List installed plugins</li>\n              <li><code>wp theme list</code> - List installed themes</li>\n              <li><code>wp db tables</code> - List database tables (requires database commands enabled)</li>\n            </ul>\n            \n            <h4>Command Structure:</h4>\n            <p>Commands follow the WP-CLI structure: <code>wp &lt;command&gt; &lt;subcommand&gt; [options]</code></p>\n            <p>You can also use the colon syntax: <code>wp:command:subcommand [options]</code></p>\n          </div>\n        </div>\n      </template>\n    </Card>\n  </div>\n</template>\n\n<script setup>\nimport { onMounted, reactive } from 'vue';\nimport { useToast } from 'primevue/usetoast';\nimport { $get, $post } from '../request';\n\nconst toast = useToast();\n\nconst state = reactive({\n  isLoading: true,\n  terminalEnabled: true,\n  dbCommandsEnabled: false\n});\n\nasync function fetchSettings() {\n  try {\n    state.isLoading = true;\n    const args = {\n      route: 'get_terminal_settings'\n    };\n    \n    const { data, error } = await $get(args);\n    \n    if (data && data.value && data.value.data) {\n      state.terminalEnabled = data.value.data.terminal_enabled;\n      state.dbCommandsEnabled = data.value.data.db_commands_enabled;\n    } else if (error) {\n      showToast('error', 'Error', 'Failed to load terminal settings');\n      console.error('Error fetching terminal settings:', error);\n    }\n  } catch (err) {\n    showToast('error', 'Error', 'Failed to load terminal settings');\n    console.error('Error fetching terminal settings:', err);\n  } finally {\n    state.isLoading = false;\n  }\n}\n\nasync function updateSettings() {\n  try {\n    const args = {\n      route: 'update_terminal_settings',\n      terminal_enabled: state.terminalEnabled,\n      db_commands_enabled: state.dbCommandsEnabled\n    };\n    \n    const { data, error } = await $post(args);\n    \n    if (data && data.value && data.value.data && data.value.data.success) {\n      showToast('success', 'Success', data.value.data.message || 'Terminal settings updated successfully');\n    } else if (error) {\n      showToast('error', 'Error', 'Failed to update terminal settings');\n      console.error('Error updating terminal settings:', error);\n    }\n  } catch (err) {\n    showToast('error', 'Error', 'Failed to update terminal settings');\n    console.error('Error updating terminal settings:', err);\n  }\n}\n\nfunction showToast(severity, summary, detail) {\n  toast.add({\n    severity: severity,\n    summary: summary,\n    detail: detail,\n    life: 3000\n  });\n}\n\nonMounted(() => {\n  fetchSettings();\n});\n</script>\n\n<style scoped>\n.terminal-settings-view {\n  max-width: 1200px;\n  margin: 0 auto;\n  padding: 20px;\n}\n\n.loading-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px;\n}\n\n.settings-container {\n  padding: 10px;\n}\n\n.setting-item {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n}\n\n.setting-info {\n  flex: 1;\n}\n\n.setting-info h3 {\n  margin-top: 0;\n  margin-bottom: 10px;\n}\n\n.setting-info p {\n  margin-top: 0;\n  color: #666;\n}\n\n.setting-control {\n  margin-left: 20px;\n}\n\n.terminal-info {\n  background-color: #f8f9fa;\n  border-radius: 6px;\n  padding: 20px;\n  margin-top: 20px;\n}\n\n.terminal-info h3 {\n  margin-top: 0;\n  margin-bottom: 15px;\n}\n\n.terminal-info h4 {\n  margin-top: 20px;\n  margin-bottom: 10px;\n}\n\n.terminal-info ul {\n  margin: 0;\n  padding-left: 20px;\n}\n\n.terminal-info li {\n  margin-bottom: 5px;\n}\n\ncode {\n  background-color: #e9ecef;\n  padding: 2px 6px;\n  border-radius: 4px;\n  font-family: monospace;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
