ss=\"pi pi-envelope\"></i>\n                                </span>\n                                <InputText\n                                    id=\"email\"\n                                    v-model=\"state.email\"\n                                    placeholder=\"Enter email address\"\n                                    aria-describedby=\"email-help\"\n                                />\n                            </div>\n                            <small id=\"email-help\" class=\"form-text\">Email address to receive notifications</small>\n                        </div>\n\n                        <Divider />\n\n                        <div class=\"notification-options\">\n                            <div class=\"option-item\">\n                                <div class=\"option-content\">\n                                    <h4>Daily Summary</h4>\n                                    <p>Receive a daily email summary if any debug logs were created</p>\n                                </div>\n                                <InputSwitch\n                                    v-model=\"state.status\"\n                                    @change=\"updatetNotificationEmail\"\n                                />\n                            </div>\n                        </div>\n\n                        <div class=\"form-actions\">\n                            <Button\n                                label=\"Save Settings\"\n                                icon=\"pi pi-check\"\n                                @click=\"updatetNotificationEmail\"\n                                :loading=\"state.isSaving\"\n                                :disabled=\"!state.email\"\n                            />\n                            <Button\n                                label=\"Test Email\"\n                                icon=\"pi pi-envelope\"\n                                class=\"p-button-outlined\"\n                                @click=\"sendTestEmail\"\n                                :disabled=\"!state.email\"\n                            />\n                        </div>\n                    </div>\n                </div>\n            </template>\n        </Card>\n    </div>\n</template>\n\n<script setup>\nimport { ref, reactive, onMounted } from 'vue';\nimport { $post, $get } from \"../request\";\nimport { useToast } from \"primevue/usetoast\";\n\nconst toast = useToast();\n\nconst state = reactive({\n    email: null,\n    status: null,\n    error: null,\n    isLoading: false,\n    isSaving: false\n});\n\nasync function fetchNotificationEmail() {\n    try {\n        state.isLoading = true;\n        const args = {\n            route: 'get_notification_email'\n        };\n        const { data, error: fetchError } = await $get(args);\n        if (data && data.value && data.value.data) {\n            state.email = data.value.data.email;\n            state.status = data.value.data.status;\n        } else if (fetchError) {\n            state.error = fetchError;\n            showToast('error', 'Error', 'Failed to load notification settings');\n        }\n    } catch (err) {\n        state.error = err;\n        showToast('error', 'Error', 'Failed to load notification settings');\n    } finally {\n        state.isLoading = false;\n    }\n}\n\nasync function updatetNotificationEmail() {\n    try {\n        state.isSaving = true;\n        const args = {\n            route: 'update_notification_email',\n            status: state.status,\n            email: state.email\n        };\n        const { data, error: fetchError } = await $post(args);\n        if (data && data.value && data.value.data) {\n            showToast(\n                data.value.data.success ? 'success' : 'warn',\n                data.value.data.success ? 'Success' : 'Warning',\n                data.value.data.message || 'Notification settings updated'\n            );\n        } else if (fetchError) {\n            state.error = fetchError;\n            showToast('error', 'Error', 'Failed to update notification settings');\n        }\n    } catch (err) {\n        state.error = err;\n        showToast('error', 'Error', 'Failed to update notification settings');\n    } finally {\n        state.isSaving = false;\n    }\n}\n\nasync function sendTestEmail() {\n    try {\n        if (!state.email) {\n            showToast('warn', 'Warning', 'Please enter an email address first');\n            return;\n        }\n\n        showToast('info', 'Sending', 'Sending test email to ' + state.email);\n\n        // Here you would implement the actual test email sending\n        // For now, we'll just simulate it with a timeout\n        setTimeout(() => {\n            showToast('success', 'Success', 'Test email sent to ' + state.email);\n        }, 1500);\n    } catch (err) {\n        showToast('error', 'Error', 'Failed to send test email');\n    }\n}\n\nfunction showToast(severity, summary, detail) {\n    toast.add({\n        severity: severity,\n        summary: summary,\n        detail: detail,\n        group: 'br',\n        life: 3000\n    });\n}\n\nonMounted(() => {\n    fetchNotificationEmail();\n});\n</script>\n\n<style scoped>\n.notification-view {\n    max-width: 1200px;\n    margin: 0 auto;\n    padding: 20px;\n}\n\n.card-title {\n    display: flex;\n    align-items: center;\n    font-size: 1.25rem;\n}\n\n.loading-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    padding: 40px;\n}\n\n.notification-content {\n    padding: 10px;\n}\n\n.notification-form {\n    margin-top: 20px;\n}\n\n.form-field {\n    margin-bottom: 1.5rem;\n}\n\n.form-label {\n    display: block;\n    margin-bottom: 0.5rem;\n    font-weight: 600;\n    color: #4b5563;\n}\n\n.form-text {\n    display: block;\n    margin-top: 0.5rem;\n    color: #6b7280;\n}\n\n.notification-options {\n    margin: 1.5rem 0;\n}\n\n.option-item {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 1rem;\n    background-color: #f9fafb;\n    border-radius: 8px;\n    margin-bottom: 1rem;\n}\n\n.option-content h4 {\n    margin: 0 0 0.5rem 0;\n    color: #374151;\n}\n\n.option-content p {\n    margin: 0;\n    color: #6b7280;\n}\n\n.form-actions {\n    display: flex;\n    gap: 1rem;\n    margin-top: 2rem;\n}\n\n/* Responsive adjustments */\n@media (max-width: 768px) {\n    .option-item {\n        flex-direction: column;\n        align-items: flex-start;\n    }\n\n    .option-item .p-inputswitch {\n        margin-top: 1rem;\n    }\n\n    .form-actions {\n        flex-direction: column;\n    }\n\n    .form-actions .p-button {\n        width: 100%;\n    }\n}\n</style>"],"sourceRoot":""}]);
