\n    text-align: right;\n    margin-right: 0.5rem;\n}\n.frame-file[data-v-22120d80] {\n    color: #1e40af;\n    margin-right: 0.25rem;\n}\n.frame-line[data-v-22120d80] {\n    color: #6b7280;\n    margin-right: 0.5rem;\n}\n.frame-function[data-v-22120d80] {\n    color: #047857;\n    font-weight: 600;\n}\n.highlight-frame[data-v-22120d80] {\n    background-color: #ecfdf5;\n}\n.frame-details[data-v-22120d80] {\n    margin-top: 0.5rem;\n    padding: 0.5rem;\n    background-color: #ffffff;\n    border: 1px solid #e5e7eb;\n    border-radius: 4px;\n}\n.frame-args[data-v-22120d80], .frame-context[data-v-22120d80] {\n    margin-top: 0.5rem;\n}\n.args-header[data-v-22120d80], .context-header[data-v-22120d80] {\n    font-weight: 600;\n    margin-bottom: 0.25rem;\n    color: #4b5563;\n}\n.arg-item[data-v-22120d80] {\n    display: flex;\n    margin-bottom: 0.25rem;\n    font-family: monospace;\n    font-size: 0.85rem;\n}\n.arg-index[data-v-22120d80] {\n    width: 2rem;\n    color: #6b7280;\n}\n.arg-value[data-v-22120d80] {\n    color: #1e3a8a;\n}\n.context-code[data-v-22120d80] {\n    margin: 0;\n    padding: 0.5rem;\n    background-color: #f3f4f6;\n    border-radius: 4px;\n    font-size: 0.85rem;\n    overflow-x: auto;\n}\n", "",{"version":3,"sources":["webpack://./resources/components/StackTraceViewer.vue"],"names":[],"mappings":";AAqPA;IACI,cAAc;IACd,yBAAyB;IACzB,kBAAkB;IAClB,yBAAyB;AAC7B;AAEA;IACI,aAAa;IACb,cAAc;IACd,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;AACf;AAEA;IACI,eAAe;IACf,cAAc;IACd,qBAAqB;AACzB;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;AAClB;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,cAAc;IACd,qBAAqB;AACzB;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,qBAAqB;IACrB,yBAAyB;IACzB,gCAAgC;AACpC;AAEA;IACI,SAAS;IACT,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;AAEA;IACI,eAAe;IACf,iBAAiB,EAAE,qDAAqD;IACxE,gBAAgB;AACpB;AAEA;IACI,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;AAEA;IACI,eAAe;IACf,gCAAgC;IAChC,eAAe;AACnB;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,iBAAiB;AACrB;AAEA;IACI,qBAAqB;IACrB,WAAW;IACX,cAAc;IACd,iBAAiB;IACjB,oBAAoB;AACxB;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;AAEA;IACI,cAAc;IACd,oBAAoB;AACxB;AAEA;IACI,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,yBAAyB;IACzB,yBAAyB;IACzB,kBAAkB;AACtB;AAEA;IACI,kBAAkB;AACtB;AAEA;IACI,gBAAgB;IAChB,sBAAsB;IACtB,cAAc;AAClB;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,kBAAkB;AACtB;AAEA;IACI,WAAW;IACX,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,SAAS;IACT,eAAe;IACf,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;IAClB,gBAAgB;AACpB","sourcesContent":["<template>\n    <div class=\"stack-trace-viewer\">\n        <div v-if=\"!stackTrace || stackTrace.length === 0\" class=\"no-stack-trace\">\n            <div class=\"no-trace-icon\"><i class=\"pi pi-exclamation-circle\"></i></div>\n            <div class=\"no-trace-message\">No stack trace available</div>\n            <div class=\"no-trace-help\">Try enabling WP_DEBUG_BACKTRACE in your settings to get more detailed stack traces.</div>\n        </div>\n        <div v-else class=\"stack-trace-container\">\n            <div class=\"stack-trace-header\">\n                <h3>Stack Trace</h3>\n                <Button @click=\"copyToClipboard\" size=\"small\" icon=\"pi pi-copy\" label=\"Copy\" severity=\"secondary\"/>\n            </div>\n            <div class=\"stack-trace-content\">\n                <ul class=\"stack-frames\">\n                    <li v-for=\"(frame, index) in parsedStackTrace\" :key=\"index\"\n                        :class=\"{ 'highlight-frame': frame.isHighlighted }\"\n                        @click=\"toggleFrameDetails(index)\">\n                        <div class=\"frame-header\">\n                            <span class=\"frame-number\">{{ index + 1 }}</span>\n                            <span class=\"frame-file\">{{ frame.file }}</span>\n                            <span class=\"frame-line\">:{{ frame.line }}</span>\n                            <span class=\"frame-function\">{{ frame.function }}</span>\n                        </div>\n                        <div v-if=\"expandedFrames[index]\" class=\"frame-details\">\n                            <div v-if=\"frame.args && frame.args.length > 0\" class=\"frame-args\">\n                                <div class=\"args-header\">Arguments:</div>\n                                <div v-for=\"(arg, argIndex) in frame.args\" :key=\"argIndex\" class=\"arg-item\">\n                                    <span class=\"arg-index\">{{ argIndex + 1 }}:</span>\n                                    <span class=\"arg-value\">{{ arg }}</span>\n                                </div>\n                            </div>\n                            <div v-if=\"frame.context && frame.context.length > 0\" class=\"frame-context\">\n                                <div class=\"context-header\">Code Context:</div>\n                                <pre class=\"context-code\">{{ frame.context.join('\\n') }}</pre>\n                            </div>\n                        </div>\n                    </li>\n                </ul>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script setup>\nimport { ref, computed, onMounted } from 'vue';\nimport { useToast } from 'primevue/usetoast';\n\nconst props = defineProps({\n    stackTrace: {\n        type: Array,\n        default: () => []\n    }\n});\n\nconst toast = useToast();\nconst expandedFrames = ref({});\n\n// Parse the stack trace into a more structured format\nconst parsedStackTrace = computed(() => {\n    if (!props.stackTrace || props.stackTrace.length === 0) {\n        return [];\n    }\n\n    return props.stackTrace.map((frame, index) => {\n        // Parse the frame string to extract file, line, function, etc.\n        const parsedFrame = parseStackFrame(frame);\n\n        // Highlight WordPress core frames differently\n        parsedFrame.isHighlighted = isWordPressCore(parsedFrame.file);\n\n        return parsedFrame;\n    });\n});\n\n// Parse a stack frame string into components\nfunction parseStackFrame(frameString) {\n        // Default structure if parsing fails\n    const defaultFrame = {\n        file: 'Unknown',\n        line: 0,\n        function: frameString,\n        args: [],\n        context: []\n    };\n\n    // If the frame is a message from our custom fallback, handle it specially\n    if (frameString.startsWith('No stack trace information') ||\n        frameString.startsWith('-') ||\n        frameString === 'This may be because:' ||\n        frameString === 'Stack trace:') {\n        defaultFrame.file = 'Info';\n        defaultFrame.function = frameString;\n        defaultFrame.isHighlighted = false;\n        return defaultFrame;\n    }\n\n    // If the frame is a continuation indicator (...), handle it specially\n    if (frameString.startsWith('...')) {\n        defaultFrame.file = 'Continuation';\n        defaultFrame.function = frameString;\n        defaultFrame.isHighlighted = false;\n        return defaultFrame;\n    }\n\n    try {\n        // Handle variable dump lines\n        if (frameString.startsWith('$') || frameString.startsWith('Variable dump')) {\n            defaultFrame.file = 'Variable dump';\n            defaultFrame.function = frameString;\n            defaultFrame.isHighlighted = true;\n            return defaultFrame;\n        }\n\n        // Handle error type information\n        if (frameString.startsWith('\\nError Type:')) {\n            defaultFrame.file = 'Error Info';\n            defaultFrame.function = frameString.replace('\\nError Type:', 'Error Type:');\n            defaultFrame.isHighlighted = true;\n            return defaultFrame;\n        }\n\n        // Handle the \"thrown in\" line differently\n        if (frameString.includes('thrown in')) {\n            const thrownMatch = frameString.match(/thrown in ([^\\s]+) on line (\\d+)/);\n            if (thrownMatch && thrownMatch.length >= 3) {\n                defaultFrame.file = thrownMatch[1];\n                defaultFrame.line = parseInt(thrownMatch[2], 10);\n                defaultFrame.function = 'Exception thrown';\n                defaultFrame.isHighlighted = true;\n                return defaultFrame;\n            }\n        }\n\n        // Handle standard stack trace format\n        // Example: \"#0 /path/to/file.php(123): SomeClass->someMethod(arg1, arg2)\"\n        const frameNumberMatch = frameString.match(/^#(\\d+)\\s+/);\n        if (frameNumberMatch) {\n            // Remove the frame number prefix for further parsing\n            const withoutNumber = frameString.replace(/^#\\d+\\s+/, '');\n\n            // Try to parse common stack trace formats\n            // Example: \"/path/to/file.php(123): SomeClass->someMethod(arg1, arg2)\"\n            const fileMatch = withoutNumber.match(/^(.*?)\\((\\d+)\\):/);\n            const functionMatch = withoutNumber.match(/\\): (.*?)(\\(.*\\))?$/);\n\n            if (fileMatch && fileMatch.length >= 3) {\n                defaultFrame.file = fileMatch[1];\n                defaultFrame.line = parseInt(fileMatch[2], 10);\n            }\n\n            if (functionMatch && functionMatch.length >= 2) {\n                defaultFrame.function = functionMatch[1];\n\n                // Try to extract arguments if present\n                if (functionMatch[2]) {\n                    const argsString = functionMatch[2].replace(/^\\(|\\)$/g, '');\n                    defaultFrame.args = argsString.split(',').map(arg => arg.trim());\n                }\n            }\n\n            // If we couldn't parse the function, use the whole string after the file part\n            if (!functionMatch && withoutNumber.includes(':')) {\n                const parts = withoutNumber.split(':');\n                if (parts.length >= 2) {\n                    defaultFrame.function = parts.slice(1).join(':').trim();\n                }\n            }\n        } else {\n            // Try to handle other formats\n            // Example: \"/path/to/file.php(123): SomeClass->someMethod(arg1, arg2)\"\n            const fileMatch = frameString.match(/^(.*?)\\((\\d+)\\):/);\n            if (fileMatch && fileMatch.length >= 3) {\n                defaultFrame.file = fileMatch[1];\n                defaultFrame.line = parseInt(fileMatch[2], 10);\n\n                const functionMatch = frameString.match(/\\): (.*?)(\\(.*\\))?$/);\n                if (functionMatch && functionMatch.length >= 2) {\n                    defaultFrame.function = functionMatch[1];\n\n                    // Try to extract arguments if present\n                    if (functionMatch[2]) {\n                        const argsString = functionMatch[2].replace(/^\\(|\\)$/g, '');\n                        defaultFrame.args = argsString.split(',').map(arg => arg.trim());\n                    }\n                }\n            } else if (frameString.includes('=')) {\n                // This might be a variable dump line\n                const parts = frameString.split('=');\n                if (parts.length >= 2) {\n                    defaultFrame.file = 'Variable';\n                    defaultFrame.function = frameString;\n                    defaultFrame.isHighlighted = true;\n                }\n            } else {\n                // If all else fails, just use the raw string\n                defaultFrame.function = frameString;\n            }\n        }\n    } catch (e) {\n        console.error('Error parsing stack frame:', e);\n    }\n\n    // Don't log every parsed frame to avoid console spam\n    // console.log('Parsed frame:', defaultFrame);\n    return defaultFrame;\n}\n\n// Check if a file path is part of WordPress core\nfunction isWordPressCore(filePath) {\n    return filePath.includes('/wp-includes/') ||\n           filePath.includes('/wp-admin/') ||\n           filePath.includes('/wp-content/');\n}\n\n// Toggle the expanded state of a frame\nfunction toggleFrameDetails(frameIndex) {\n    expandedFrames.value = {\n        ...expandedFrames.value,\n        [frameIndex]: !expandedFrames.value[frameIndex]\n    };\n}\n\n// Copy the stack trace to clipboard\nfunction copyToClipboard() {\n    const text = props.stackTrace.join('\\n');\n    navigator.clipboard.writeText(text).then(() => {\n        toast.add({\n            severity: 'success',\n            summary: 'Copied',\n            detail: 'Stack trace copied to clipboard',\n            life: 3000\n        });\n    }).catch(err => {\n        console.error('Failed to copy:', err);\n        toast.add({\n            severity: 'error',\n            summary: 'Error',\n            detail: 'Failed to copy to clipboard',\n            life: 3000\n        });\n    });\n}\n</script>\n\n<style scoped>\n.stack-trace-viewer {\n    margin: 1rem 0;\n    border: 1px solid #e5e7eb;\n    border-radius: 4px;\n    background-color: #f9fafb;\n}\n\n.no-stack-trace {\n    padding: 2rem;\n    color: #6b7280;\n    text-align: center;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 0.5rem;\n}\n\n.no-trace-icon {\n    font-size: 2rem;\n    color: #9ca3af;\n    margin-bottom: 0.5rem;\n}\n\n.no-trace-message {\n    font-size: 1.25rem;\n    font-weight: 600;\n    color: #4b5563;\n}\n\n.no-trace-help {\n    font-size: 0.875rem;\n    color: #6b7280;\n    max-width: 80%;\n    margin: 0.5rem auto 0;\n}\n\n.stack-trace-container {\n    display: flex;\n    flex-direction: column;\n}\n\n.stack-trace-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 0.75rem 1rem;\n    background-color: #f3f4f6;\n    border-bottom: 1px solid #e5e7eb;\n}\n\n.stack-trace-header h3 {\n    margin: 0;\n    font-size: 1rem;\n    font-weight: 600;\n    color: #374151;\n}\n\n.stack-trace-content {\n    padding: 0.5rem;\n    max-height: 600px; /* Increased height to show more of the stack trace */\n    overflow-y: auto;\n}\n\n.stack-frames {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n.stack-frames li {\n    padding: 0.5rem;\n    border-bottom: 1px solid #e5e7eb;\n    cursor: pointer;\n}\n\n.stack-frames li:last-child {\n    border-bottom: none;\n}\n\n.stack-frames li:hover {\n    background-color: #f3f4f6;\n}\n\n.frame-header {\n    display: flex;\n    align-items: center;\n    font-family: monospace;\n    font-size: 0.9rem;\n}\n\n.frame-number {\n    display: inline-block;\n    width: 2rem;\n    color: #6b7280;\n    text-align: right;\n    margin-right: 0.5rem;\n}\n\n.frame-file {\n    color: #1e40af;\n    margin-right: 0.25rem;\n}\n\n.frame-line {\n    color: #6b7280;\n    margin-right: 0.5rem;\n}\n\n.frame-function {\n    color: #047857;\n    font-weight: 600;\n}\n\n.highlight-frame {\n    background-color: #ecfdf5;\n}\n\n.frame-details {\n    margin-top: 0.5rem;\n    padding: 0.5rem;\n    background-color: #ffffff;\n    border: 1px solid #e5e7eb;\n    border-radius: 4px;\n}\n\n.frame-args, .frame-context {\n    margin-top: 0.5rem;\n}\n\n.args-header, .context-header {\n    font-weight: 600;\n    margin-bottom: 0.25rem;\n    color: #4b5563;\n}\n\n.arg-item {\n    display: flex;\n    margin-bottom: 0.25rem;\n    font-family: monospace;\n    font-size: 0.85rem;\n}\n\n.arg-index {\n    width: 2rem;\n    color: #6b7280;\n}\n\n.arg-value {\n    color: #1e3a8a;\n}\n\n.context-code {\n    margin: 0;\n    padding: 0.5rem;\n    background-color: #f3f4f6;\n    border-radius: 4px;\n    font-size: 0.85rem;\n    overflow-x: auto;\n}\n</style>\n"],"sourceRoot":""}]);
